<template>
  <div class="activities-publish">
    <div class="line"></div>
    <div class="part-one">
      <mu-text-field label="" labelFloat v-model="actMsg.title"/><br/>
      <mu-text-field label="" v-model="actMsg.detail" labelFloat multiLine :rows="6" :rowsMax="8" :maxLength="100" :errorText="multiLineInputErrorText" @textOverflow="handleMultiLineOverflow"/>
      <mu-icon value="camera_alt"/>
    </div>
    <div class="line"></div>
    <div class="part-two">
      <div class="set-date">
        <mu-date-picker labelFloat v-model="actMsg.beginDate" label=""/>
        <mu-date-picker labelFloat v-model="actMsg.endDate" label=""/>
      </div>
      <mu-date-picker labelFloat v-model="actMsg.closeDate" label=""/>
      <mu-text-field labelFloat v-model="actMsg.address" label=""/>
      <mu-text-field  labelFloat v-model="actMsg.budget" label=""/>
      <mu-select-field v-model="actMsg.team" multiple labelFloat label="">
        <mu-menu-item v-for="(item,key) in actMsg.team" :key="key" :value="item.id" :title="item.name"/>
      </mu-select-field>
      <div class="audit-text">
        <span></span>
        <mu-switch v-model="actMsg.audit" label="" />
      </div>
    </div>
    <mu-raised-button label="" class="publish-btn" fullWidth/>
  </div>
</template>
<script>
export default {
  data() {
    return {
      actMsg: {
        title: '',
        team:[],
        people: '',
        pic: '',
        detail: '',
        address: '',
        beginDate: '',
        endDate: '',
        closeDate: '',
        budget: '',
        audit: false
      },
      multiLineInputErrorText: '',
      actId: ''
    };
  },
  methods:{
    handleMultiLineOverflow (isOverflow) {
      this.multiLineInputErrorText = isOverflow ? '100' : ''
    }
  },
  created(){
    this.actId = this.$route.params.id;
    this.$root.eventHub.$emit('showBottom',false);
    if (this.actId) { //
        this.$root.eventHub.$emit('setAppbar',{
            title: '',
            isBack: true,
            backUrl: '#/activity/:'+this.actId
        });
        // 
        this.actMsg = {
          id: 1,
          title: '',
          game2:[{
            name: '',
            id: 1
          },{
            name: '',
            id: 2
          }],
          status: 1,
          people: 23,
          pic: '/static/images/bg.jpg',
          detail: '',
          address: '',
          beginDate: '2017/12/12',
          endDate: '2017/12/18',
          closeDate: '2017/12/12',
          budget: '100',
          audit: true
        }
    } else {
      this.$root.eventHub.$emit('setAppbar',{
          title: '',
          isBack: true,
          backUrl: '#/activity'
      });
      // 
      this.team = [{
        name: '',
        value: 1
      },{
        name: '',
        value: 2
      },{
        name: '',
        value: 3
      }]
    }
  }
}
</script>
<style lang="scss" ref="stylesheet/scss">
@import './static/css/style.scss';
  .activities-publish{
    .line{
      width: 100%;
      height: 8px;
      background: $bg-color;
    }
    .part-one{
      background: #fff;
      padding: 0 24px;
      @extend %p-r;
    }
    .mu-text-field{
      width: 100%;
    }
    .mu-icon.material-icons {
      @extend %p-a;
      bottom: 30px;
      left: 24px;
    }
    .fonts{
      @extend %p-a;
      bottom: 30px;
      right: 24px;
    }
    .part-two{
      padding: 0 24px 20px;
      @extend %p-r;
      .set-date{
        @extend %d-f;
        margin-top: 20px;
        .mu-date-picker{
          @extend %f-1;
          width: auto;
        }
      }
      .set-date .mu-date-picker:first-child{
        margin-right: 10px;
      }
      .mu-date-picker{
          width: 100%;
        }
    }
    .audit-text{
      @extend %d-f;
      span{
        @extend %d-i-b;
        padding-top: 28px;
        padding-bottom: 12px;
        color: rgba(0,0,0,0.54);
        @extend %f-1;
      }
      label{
        width: 40px;
        padding-top: 28px;
      }
    }
    .publish-btn{
      background: $main-color;
      .mu-raised-button-label{
        color: #fff;
      }
      @extend %p-f;
      bottom: 0;
      z-index: 5;
      height: 44px;
      line-height: 44px;
    }
    .mu-text-field-help{
      margin-top: -26px;
      div:first-child{
        padding-left: 40px;
      }
    }
    textarea.mu-text-field-input{
      margin-bottom: 30px;
    }
  }
</style>



