<template>
  <div class="activities-comment">
    <mu-list class="del-list">
          <div class="item">
            <mu-icon value="message"/>
            <mu-list-item title="" class="title"/>
            <span>({{comment.list}})</span>
          </div>
          <mu-list-item title="Alex Qin">
            <mu-avatar src="/static/images/avatar1.png" slot="leftAvatar"/>
            <span slot="describe">
              <span style="color: rgba(0, 0, 0, .87)">?</span>
              <p>,</p>
            </span>
            <mu-icon-menu slot="right" icon="more_vert" tooltip="">
              <mu-menu-item title="" />
              <mu-menu-item title="" />
              <mu-menu-item title="" />
            </mu-icon-menu>
          </mu-list-item>
          <mu-divider inset/>
          <mu-list-item title="Alex Qin">
            <mu-avatar src="/static/images/avatar1.png" slot="leftAvatar"/>
            <span slot="describe">
              <span style="color: rgba(0, 0, 0, .87)"></span> <br/>
              
            </span>
            <mu-icon-menu slot="right" icon="more_vert" tooltip="">
              <mu-menu-item title="" />
              <mu-menu-item title="" />
              <mu-menu-item title="" />
            </mu-icon-menu>
          </mu-list-item>
          <mu-divider inset/>
          <mu-list-item title="LOL">
            <mu-avatar src="/static/images/avatar1.png" slot="leftAvatar"/>
            <span slot="describe">
              <span style="color: rgba(0, 0, 0, .87)"></span><br/>
               LOL
            </span>
            <mu-icon-menu slot="right" icon="more_vert" tooltip="">
              <mu-menu-item title="" />
              <mu-menu-item title="" />
              <mu-menu-item title="" />
            </mu-icon-menu>
          </mu-list-item>
    </mu-list>
  </div>
</template>
<script>
export default {
  data() {
      return {
        comment: {
            list: 23,
            message:[{
            title: '',
            }]
        },
        actId:''
      }
  },
  created(){
    this.actId = this.$route.params.id;
    this.$root.eventHub.$emit('setAppbar',{
        title: '',
        isBack: true,
        backUrl: '#/activity/:'+this.actId
    });
    this.$root.eventHub.$emit('showBottom',false);
  }
}
</script>
<style lang="scss">
@import './static/css/style.scss';
.activities-comment{
     .del-list{
        .item{
          @extend %d-f;
          i{
            @extend %v-a-m;
            margin:12px 0 0 10px;
            width: 24px;
          }
          span{
            line-height: 48px;
          }
        }
        padding: 10px;
        margin-top: 5px;
    }
   
}
</style>



